.review {
  display: flex;
  flex-direction: row;
  .review__item {
    margin-right: 1.4rem;
  }

  .review__item-title {
    @include h3($dark2);
    margin-bottom: 1.1rem;
  }
  .review__item-imgLink {
    width: 196px;
    height: 142px;
    box-shadow: 0px 5px 5px 0 rgba(0, 0, 0, 0.3);
    border: solid 1px #ffffff;
  }
  .review__item-imgLink > img {
    object-fit: cover;
  }
  .review__item-imgLink > a {
    cursor: pointer;
  }
}
